<template>
  <div name="fade" class="pickable-image">
    <!--modal v-if="show_modal" @close="show_modal = false" :image_url="source_url">
      <h3 slot="header">{{ image_url }}</h3>
    </modal-->
    <div class="column" @click="clipboard_url">
      <transition appear>
        <b-img-lazy fluid :src="image_url" :alt="name" class="rounded_image"></b-img-lazy>
      </transition>
    </div>
  </div>
</template>

<script>
/*import Modal from "@/components/Modal";*/
import miniToastr from 'mini-toastr'

export default {
  name: "PickableImage",
  components: {},
  notifications: {
    showSuccessMsg: {
      type: 'error',
      title: 'Hello there',
      message: 'That\'s the success!'
    }
  },
  props: {
    json_data: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      show_modal: false
    }
  },
  computed: {
    name() {
      return this.json_data['filename']
    },
    source_url() {
      return this.json_data['url']
    },
    image_url() {
      return `https://raw.githubusercontent.com/attendeany/opf-icons/main/icons/${this.name}`
    }
  },
  methods: {
    show_full_image() {
      console.log(`Opening ${this.source_url}`)
    },
    clipboard_url() {
      navigator.clipboard.writeText(this.source_url)
      miniToastr.info(this.source_url, "URL copied")
    }
  }
}
</script>

<style scoped>

.rounded_image {
  border-radius: 10px;
}

.column {
  flex: 25%;
  max-width: 25%;
  padding: 0 4px;
  margin-bottom: 4px;
  cursor: pointer;
}

@media screen and (max-width: 800px) {
  .column {
    flex: 50%;
    max-width: 50%;
  }
}

@media screen and (max-width: 600px) {
  .column {
    flex: 100%;
    max-width: 100%;
  }
}

.mini-toastr {
  right: 12px !important;
  top: inherit !important;
  left: inherit !important;
  bottom: 12px !important;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}

.fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */
{
  opacity: 0;
}

</style>